var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("尤达大师")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        尤达是一个虚构人物星球大战专营通过创建乔治·卢卡斯，在1980影片首先出现帝国反击战。在原版电影中，他训练卢克天行者与银河帝国作战。在电影的前传，他作为绝地大法师和作为的高级将领克隆警官在克隆战争。在他返回绝地之前在900岁时，尤达是星球大战中最古老的角色，直到在原力觉醒中引入Maz Kanata。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        弗兰克·奥兹在每部电影中都提供了尤达的声音，并在原版三部曲和“星球大战：第一集 - 幻影威胁”中运用了他作为木偶的技巧。对于第五集和第一集中的一些步行场景，矮人演员Deep Roy和Warwick Davis作为Yoda出现在服装中（尽管两者都没有被记入）。虽然弗兰克·奥兹担任主要表演者，但他得到了众多其他木偶的帮助，包括：凯瑟琳马伦（前五），温迪弗鲁德（前五），大卫巴克莱（前五 - 六），迈克奎因 （Ep.VI），David Greenaway（Ep。I＆VI），Don Austen（Ep。I）和Kathy Smee（Ep.I）。对于无线戏剧化的帝国反击战和绝地归来，尤达是由浊约翰·利思戈，而汤姆·凯恩在他说出克隆人战争动画连续剧，几个视频游戏，以及一系列星球大战：克隆人战争。乔治卢卡斯最初认为尤达的全名是“ Minch Yoda”“在缩短之前。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        绝地大师尤达是星球大战宇宙中最古老，最坚忍，最强大的杰迪大师之一。系列创作者乔治卢卡斯选择了许多关于角色生活史的细节仍然未知。尤达的种族和家庭世界并没有在任何官方媒体，规范或其他方面被命名，他只是被称为星球大战数据库的“未知物种” 。尤达的特色语音模式已经被学术语法学家分析和讨论，他们发现它有点不一致，但可以推断它有对象 - 主语 - 动词词序 使它成为一个anastrophe。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        这部电影和扩展的宇宙揭示了他几乎培养了星系中的每一位绝地大师，包括杜库伯爵，他在克隆人的攻击中被认定为尤达的老Padawan学习者 ; 梅斯温杜 ; Obi-Wan Kenobi（部分地，在Qui-Gon Jinn接任Obi-Wan的主人之前）;Ki-Adi-Mundi，Kit Fisto和最终Luke Skywalker。在动画系列星球大战：克隆人战争期间，设置在克隆人的攻击和西斯的复仇，他提到他训练了绝地委员会的另一位领导人，奥普兰西斯主人。在“星球大战”前传电影中，他指导了绝地圣殿中的几个幼崽，然后才被分配到一位大师。这是在星球大战第二集：克隆人的进攻中的一个场景中展示的。在“帝国反击战”中，他提到他一直在训练绝地“800年”，这意味着他必须在此之前的一段时间里一直是绝地大师。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br')])}
